@mixin vp-xs {
  @media all and (width < 550px) {
    @content;
  }
}
  @mixin vp-s {
    @media all and ( 551px <width < 767px) {
      @content;
    }
  }
  
  @mixin vp-m {
    @media all and (768px <= width < 1149px) {
      @content;
    }
  }
  
  @mixin vp-l {
    @media all and (1150px <= width < 1400px) {
      @content;
    }
  }
  
  @mixin vp-xl {
    @media all and (width > 1401px) {
      @content;
    }
  }
  