@charset "utf-8";
@import "common";
@import "fonts";
@import "preset";

@font-face {
    font-family: neon;
    src: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/707108/neon.ttf);
  }

@mixin point-set ($width,$height,$sec,$delay) {
    position:absolute;
    width:$width;
    height:$height;
    border-radius:70%;
    background: radial-gradient(rgba(255, 255, 255, 1), transparent 50%);
    //animation:ani888 $sec $delay infinite alternate;
    //transform:translate($x,$y)
    animation-name:ani888 ;
    
}
@mixin div_img ($url) {
    background-image: url($url);
    background-size:cover;
    background-position:center;
}
$shooting-time: 2000ms;
.wrap{
    width:100%;
    height:100vh;
    position: relative;
    .logo{
        padding:25px 60px;
        position: fixed;
        z-index:999;
        @include vp-m{padding:25px 40px;}
        @include vp-s{padding:25px 40px;}
        @include vp-xs{padding:30px 20px;}
        img{
            width:45px;
            @include vp-m{width:45px;}
            @include vp-s{width:45px;}
            @include vp-xs{width:35px;}
        }
    }
    .paginations{
        //width:100%;
        height:100%;
        position: fixed;
        display:flex;
        flex-direction:column;
        justify-content:center;
        align-items:flex-end;
        z-index:999;
        right:0px;
        @include vp-s{display:none;}
        @include vp-xs{display:none;}
        .pagination{
            width:0.7rem;
            height:0.7rem;
            border-radius:70%;
            background-color:#fff;
            margin-bottom:10px;
            margin-right:13px;
            opacity:0.2;
            transition: all 0.3s ease-in;
            @include vp-m{width:0.5rem;height:0.5rem;margin-right:11.5px;}
        }.pagination_active{
            width:1rem;
            height:1rem;
            border-radius:70%;
            background-color:#fff;
            margin-bottom:10px;
            margin-right:10px;
            transition: all 0.3s ease-in;
            @include vp-m{width:0.7rem;height:0.7rem;}
        }
    }
