                    border-radius: 999px;
                    filter: drop-shadow(0 0 6px rgba(105, 155, 255, 1));
                    animation: tail $shooting-time ease-in-out,
                               shooting $shooting-time ease-in-out;
                    animation-delay:1s;
                    &:nth-child(1) {
                        top:680px;
                        left:40vh;
                    }
                }
            }
