                                    @include vp-s{position:unset;}
                                    @include vp-xs{position:unset;}
                                }
                                .skill_text{
                                    transition: 300ms ease 0.1s;
                                    opacity:1;
                                    right:10px;
                                    top:13px;
                                    @include vp-m{top:11px;right:6px;}
                                    @include vp-s{opacity:0;}
                                    @include vp-xs{opacity:0;}
                                }
                            }
