 @keyframes ani888{
    0%{
        opacity:0;
    }
    100%{opacity:1;}
 }
 @keyframes rotate {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);       
    }
}

  @keyframes tail {
    0% {
      width: 0;
    }
    
    30% {
      width: 400px;
    }
    
    100% {
      width: 0;
    }
  }
  
  @keyframes shooting {
    0% {
      transform: translateX(0);
    }
    
    100% {
      transform: translateX(1000px);
    }

  }
  
  @keyframes text_shadow {
    0% {
        text-shadow: 2px 2px 20px rgba(255, 255, 255, 0.5);     
    }
    
    100% {
        text-shadow: 2px 2px 31px rgba(255, 255, 255, 1);     
    }
  }

  @keyframes upDown {
    0% {
        opacity: 0.5;
        transform: translateY(-5px);
    }
    50% {
        opacity: 1;
        transform: translateY(5px);
    }
    
    100% {
        opacity: 0.5;
        transform: translateY(-5px);
    }
  }

  @keyframes twinkling {
    0% {
      box-shadow: 0 0 10px 8px  rgba(254, 239, 222, 0.3);
    }
  
    100% {
      box-shadow: 0 0 10px 8px rgb(254, 239, 222, 0.6);
    }
  }
  
  @keyframes twinkling2 {
    0% {
      box-shadow: 0 0 10px 8px  rgba(108, 140, 39, 0.8);
    }
  
    100% {
      box-shadow: 0 0 10px 8px rgb(108, 140, 39, 1);
    }
  }
  @keyframes twinkling3 {
    0% {
      box-shadow: 0 0 10px 8px  rgba(217, 102, 102, 0.8);
    }
  
    100% {
      box-shadow: 0 0 10px 8px rgb(217, 102, 102, 1);
    }
  }
  
  @keyframes up {
    0% {
        transform: translateY(50px);
    }
    
    100% {
        transform: translateY(0px);
    }
  }

  @keyframes neon {
    0%,
    100% {
      text-shadow: 0 0 1vw #FA1C16, 0 0 3vw #FA1C16, 0 0 10vw #FA1C16, 0 0 10vw #FA1C16, 0 0 .4vw #FED128, .5vw .5vw .1vw #806914;
      color: #FED128;
    }
