#wrap > .swiper > .swiper-pagination{
    width:100%;
    position: absolute;
    display: flex;
    justify-content:flex-end;
    top: 40px !important;
    gap:40px;
    padding-right:40px;
    @media all and (768px <= width < 1149px) {
        gap:30px;
        padding-right:20px;
    }
    @media all and (551px < width < 767px) {
        gap:20px;
        padding-right:10px;
    }
    @media all and (width < 550px) {
        gap:13px;
        padding-right:10px;
    }
}
#wrap > .swiper > .swiper-pagination > .swiper-pagination-bullet
{   
    width:unset;
    height:unset;   
    background-color:rgba(0, 255, 0, 0);
    border-radius:0%;
    text-align:left;
    color:#fff;
    font-size:1.2rem;
    font-family:'Noto Serif KR ExtraLight';
    opacity:1;
    @media all and (768px <= width < 1149px) {
        font-size:1rem;
    }
    @media all and (551px < width < 767px) {
        font-size:0.9rem;
    }
    @media all and (width < 550px) {
        font-size:2.5vw;
    }
}
#wrap > .swiper > .swiper-pagination > .swiper-pagination-bullet-active{
    position: relative;
}
#wrap > .swiper > .swiper-pagination > .swiper-pagination-bullet-active::after{
    content: "";
    position: absolute;
    bottom: -7px;
    left:0;
    width: 100%;
    height: 2px;
    background: #fff;
    animation: aaa 0.8s forwards;
}
#slide4_box > .swiper > .swiper-button-prev{
    @media all and (551px < width < 767px) {
        display:none;
    }
    @media all and (width < 550px) {
        display:none;
    }
}
#slide4_box > .swiper > .swiper-button-next{
    @media all and (551px < width < 767px) {
        display:none;
    }
    @media all and (width < 550px) {
        display:none;
    }
}
#slide4_box > .swiper > .swiper-button-prev::after{
    color:#595959 !important;
    font-weight: bold;
    font-size:3rem !important;
    
}
#slide4_box > .swiper > .swiper-button-next::after{
    color:#595959 !important;
    font-weight: bold;
    font-size:3rem !important;
}

#slide4_box > .swiper > .swiper-pagination > .swiper-pagination-bullet{
    width:1rem;
    height:0.2rem;
    background-color:#fff;
    border-radius:unset;
    bottom:0px !important;
}

@keyframes aaa{
    0% {
        width: 0;
    }
    100% {
        width: 100%;
    }
}